import Vue from 'vue'
import Router from 'vue-router'
import login from '@/view/login.vue'
import courseList from '@/view/courseList'
import registerView from '@/view/registerView'
import mainContainer from '@/view/mainContainer'
import indexPage from '@/view/indexPage'
import coursePage from '@/view/coursePage'
import workList from '@/view/workList'
import submitWorkList from '@/view/submitWorkList'
import submitWorkPage from '@/view/submitWorkPage'
import studentContainer from '@/view/student/studentContainer'
import signExam from '@/view/student/signExam'
import joinExam from '@/view/student/joinExam'
import examResult from '@/view/student/examResult'
import examExercise from '@/view/student/examExercise'
import exerciseList from '@/view/student/exerciseList'
import studentIndex from '@/view/student/studentIndex'
import examRandom from '@/view/examRandom'
import adminLogin from '@/view/admin/adminLogin'
import adminContainer from '@/view/admin/adminContainer'
import adminIndex from '@/view/admin/adminIndex'
import userList from '@/view/admin/userList'
import userAdd from '@/view/admin/userAdd'
import subjectList from '@/view/admin/subjectList'
import subjectDetails from '@/view/admin/subjectDetails'
import questionList from '@/view/admin/questionList'
import questionSingleDetail from '@/view/admin/questionSingleDetail'
import questionMultipleDetail from '@/view/admin/questionMultipleDetail'
import questionTrueFalseDetail from '@/view/admin/questionTrueFalseDetail'
import questionFillingDetail from '@/view/admin/questionFillingDetail'
import questionShortAnswerDetail from '@/view/admin/questionShortAnswerDetail'
import test from '@/view/test'
import paperList from '@/view/admin/paperList'
import paperDetails from '@/view/admin/paperDetails'
import adminExamList from '@/view/admin/adminExamList'
import examDetails from '@/view/admin/examDetails'
import examView from '@/view/student/examView'
import userAnswerList from '@/view/admin/userAnswerList'

Vue.use(Router)
export default new Router({
  routes: [{
    path: '/test',
    name: 'test',
    component: test
  },
  {
    path: '/',
    redirect: '/login_page',
  },
  {
    path: '/login_page',
    name: 'login_page',
    component: login
  },
  {
    path: '/register_view',
    name: 'register_view',
    component: registerView,
  },
  {
    path: '/main_container',
    name: 'main_container',
    redirect: '/main_container/index_page',
    component: mainContainer,
    children: [
      {
        path: 'course_list',
        component: courseList,
        name: 'course_list'
      },
      {
        path: 'index_page',
        component: indexPage,
        name: 'index_page'
      },
      {
        path: 'boutique',
        component: indexPage,
        name: 'boutique'
      },
      {
        path: 'my_boutique',
        component: indexPage,
        name: 'my_boutique'
      },
      {
        path: 'prepare_course',
        component: indexPage,
        name: 'prepare_course'
      },
      {
        path: 'student_container',
        component: studentContainer,
        name: 'student_container',
        children: [
          {
            path: 'sign_exam',
            component: signExam,
            name: 'sign_exam'
          },
          {
            path: 'join_exam',
            component: joinExam,
            name: 'join_exam'
          },
          {
            path: 'exam_exercise',
            component: examExercise,
            name: 'exam_exercise'
          }, {
            path: 'exam_random',
            component: examRandom,
            name: 'exam_random'
          }, {
            path: 'exercise_list',
            component: exerciseList,
            name: 'exercise_list'
          }, {
            path: 'student_index',
            component: studentIndex,
            name: 'student_index'
          },
        ]
      }
    ]
  },
  {
    path: '/course_page',
    component: coursePage,
    name: 'course_page',
    children: [
      {
        path: 'course_index_page',
        ocmponent: indexPage,
        name: 'course_index_page'
      },
      {
        path: 'course_interaction',
        component: indexPage,
        name: 'course_interaction'
      },
      {
        path: 'course_work',
        component: workList,
        name: 'course_work'
      },
      {
        path: 'course_topic',
        component: indexPage,
        name: 'course_topic'
      },
      {
        path: 'course_data',
        component: indexPage,
        name: 'course_data'
      },
      {
        path: 'course_test',
        component: indexPage,
        name: 'course_test'
      },
      {
        path: 'course_notice',
        component: indexPage,
        name: 'course_notice'
      },
    ]
  },

  {
    path: "/submit_work_list",
    component: submitWorkList,
    name: "submit_work_list"
  },
  {
    path: '/submit_work_page',
    component: submitWorkPage,
    name: 'submit_work_page'
  },
  {
    path: '/admin_login',
    component: adminLogin,
    name: 'admin_login'
  },
  {
    path: '/admin_container',
    component: adminContainer,
    redirect: '/admin_container/admin_index',
    name: 'admin_container',
    children: [
      {
        path: 'admin_index',
        component: adminIndex,
        name: 'admin_index'
      }, {
        path: 'user_list',
        component: userList,
        name: 'user_list'
      }, {
        path: 'user_add',
        component: userAdd,
        name: 'user_add',
        redirect: 'user_add/register_view',
        children: [{
          path: 'register_view',
          component: registerView,
          name: 'admin_register_view'
        }
        ]
      }, {
        path: 'subject_list',
        component: subjectList,
        name: 'subject_list'

      }, {
        path: 'subject_details',
        component: subjectDetails,
        name: 'subject_details'
      }, {
        path: 'question_list',
        component: questionList,
        name: 'question_list'
      }, {
        path: 'question_single_choice_edit',
        component: questionSingleDetail,
        name: 'question_single_choice_edit'
      }, {
        path: 'question_multiple_choice_edit',
        component: questionMultipleDetail,
        name: 'question_multiple_choice_edit'
      }, {
        path: 'quesiton_true_false_edit',
        component: questionTrueFalseDetail,
        name: 'quesiton_true_false_edit'
      }, {
        path: 'quesiton_filling_edit',
        component: questionFillingDetail,
        name: 'quesiton_filling_edit'
      }, {
        path: 'questin_short_answer_edit',
        component: questionShortAnswerDetail,
        name: 'questin_short_answer_edit'
      }, {
        path: 'paper_list',
        component: paperList,
        name: 'paper_list'
      }, {
        path: 'paper_details',
        component: paperDetails,
        name: 'paper_details'
      }, {
        path: 'admin_exam_list',
        component: adminExamList,
        name: 'admin_exam_list'
      }, {
        path: 'exam_edit',
        component: examDetails,
        name: 'exam_edit'
      },
      {
        path: 'user_answer_list',
        component: userAnswerList,
        name: 'user_answer_list'
      }
    ]
  },
  {
    path: "/exam_view",
    component: examView,
    name: 'exam_view'
  },
  {
    path: "/exam_result",
    component: examResult,
    name: 'exam_result'
  }
  ]
})



